<template>
  <div class="success-manual">
    <slot />
    <el-dialog
      class="success-manual__dialog"
      :visible.sync="curDialogVisible"
      append-to-body
      width="420px"
      top="25vh"
      @closed="resetCom"
    >
      <div class="success-manual__dialog__body">
        <el-form
          ref="formData"
          :model="formData"
          :rules="rules"
          label-width="110px"
          class="success-manual__form"
          label-position="left"
        >
          <el-form-item label="支付渠道" prop="payChannel">
            <el-input
              v-model="formData.payChannel"
              placeholder="请填写支付渠道"
              size="small"
              maxlength="10"
              show-word-limit
              clearable
            />
          </el-form-item>
          <el-form-item
            label="支付金额"
            prop="transferAmount"
            class="success-manual__form__transfer-amount"
          >
            <el-input
              v-model.number="formData.transferAmount"
              type="number"
              size="small"
              class="success-manual__form__transfer-amount__el-input"
              placeholder="请输入转账金额"
              clearable
            />
          </el-form-item>
          <el-form-item
            label="转账成功凭证"
            prop="transferSuccessProof"
            class="success-manual__form__tsp success-manual__form__item--last"
          >
            <uploader-list
              v-model="formData.transferSuccessProof"
              width="70px"
              height="70px"
              :max="3"
            />
          </el-form-item>
        </el-form>
      </div>
      <span slot="footer" class="success-manual__dialog__footer">
        <el-button type="primary" size="small" @click="handleSubmit"
          >提交</el-button
        >
        <el-button
          class="success-manual__dialog__cancel"
          size="small"
          @click="curDialogVisible = false"
          >取消并返回</el-button
        >
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { successManual as successManualApi } from '@/api/refund'

import UploaderList from '@/components/UploaderList'

import { statusUpdateMessage } from '@/utils/statusUpdateMessage'

const defaultFormData = {
  payChannel: '',
  transferAmount: '',
  transferSuccessProof: []
}

export default {
  components: { UploaderList },
  model: {
    prop: 'dialogVisible',
    event: 'change'
  },
  props: {
    id: {
      type: String,
      required: true
    },
    dialogVisible: {
      type: Boolean,
      default: false,
      required: true
    }
  },
  data() {
    return {
      curDialogVisible: false,
      formData: Object.assign({}, defaultFormData),
      rules: {
        payChannel: [
          { required: true, message: '请输入支付渠道', trigger: 'blur' },
          { min: 0, max: 10, message: '长度在 0 到 10 个字符', trigger: 'blur' }
        ],
        transferAmount: [
          { required: true, message: '请输入支付金额', trigger: 'blur' }
        ],
        transferSuccessProof: [
          { required: true, message: '转账成功凭证不能为空', trigger: 'change' }
        ]
      }
    }
  },
  watch: {
    dialogVisible(value) {
      if (value !== this.curDialogVisible) this.curDialogVisible = value
    },
    curDialogVisible(value) {
      this.$emit('change', value)
    }
  },
  methods: {
    successManualFn({ id, payChannel, transferAmount, transferSuccessProof }) {
      return new Promise(resolve => {
        const data = {
          pay_channel: payChannel,
          transfer_amount: transferAmount,
          transfer_success_proof: transferSuccessProof.map(item => item.url)
        }
        successManualApi(id, data).then(res => {
          statusUpdateMessage(res)
          resolve()
        })
      })
    },
    async handleSubmit() {
      this.$refs['formData'].validate(async valid => {
        if (valid) {
          await this.successManualFn({
            id: this.id,
            ...this.formData
          }).then(async () => {
            this.curDialogVisible = false
            this.$emit('closedAfter')
          })
        } else {
          return false
        }
      })
    },
    resetCom() {
      this.$refs['formData'].resetFields()
    }
  }
}
</script>

<style lang="scss">
.success-manual {
  &__dialog {
    .el-dialog {
      &__header {
        display: none;
      }
      &__body {
        padding: 20px 30px 30px 30px;
      }
      &__footer {
        text-align: center;
      }
    }
    .el-form {
      .el-form-item {
        color: #0b1100;
        &__label,
        &__content {
          line-height: 32px;
        }
      }
      .success-manual__form__transfer-amount__el-input {
        input::-webkit-input-placeholder {
          color: #f0717b;
        }
      }
    }
    .el-button + &__cancel {
      margin-left: 20px;
      color: #999;
    }
  }
}
</style>

<style lang="scss" scoped>
@import '~@/styles/mixin.scss';
@import '~@/styles/variables.scss';

.success-manual {
  &__dialog {
    &__body {
      @include ff-regular;
    }
  }
  &__form {
    &__item--last {
      margin-bottom: 0;
    }
  }
}
</style>
