import request from '@/utils/request'

export function getRefundListParams() {
  return request({
    url: '/order_refund/filter',
    method: 'get'
  })
}

export function getRefundList(data) {
  return request({
    url: '/order_refund/list',
    method: 'post',
    data
  })
}

export function getRefundProcess() {
  return request({
    url: '/order_refund/process_numbers',
    method: 'get'
    // mock: '/order_refund/process_numbers'
  })
}

// 同意退款
export function agreeRefund(id) {
  return request({
    url: `/order_refund/${id}/agree`,
    method: 'put'
    // mock: `/order_refund/${id}/agree`
  })
}

// 拒绝退款
export function refuseRefund(id, data) {
  return request({
    url: `/order_refund/${id}/refuse`,
    method: 'put',
    data
    // mock: `/order_refund/${id}/refuse`
  })
}

// 获取退款订单详情
export function getRefundDetail(id) {
  return request({
    url: `/order_refund/${id}`,
    method: 'get'
    // mock: `/order_refund/${id}`
  })
}

/**
 * 修改退款金额
 * @param {String} id
 * @param {Object: {
 *  refund_amount: Number,
 *  change_reason: String
 * }} data
 */
export function changeRefundAmount(id, data) {
  return request({
    url: `/order_refund/${id}/change_refund_amount`,
    method: 'put',
    data
    // mock: `/order_refund/${id}/change_refund_amount`
  })
}

// 申请转账
export function reTransfer(id) {
  return request({
    url: `/order_refund/${id}/re_transfer`,
    method: 'get'
  })
}

/**
 * 人工退款处理成功
 * @param {String} id
 * @param {Object: {
 *  pay_channel: Number,
 *  transfer_amount: String,
 *  transfer_success_proof: [string]
 * }} data
 */
export function successManual(id, data) {
  return request({
    url: `/order_refund/${id}/success_manual`,
    method: 'put',
    data
    // mock: `/order_refund/${id}/success_manual`
  })
}

/**
 * 通过订单idArr.join(',')获取订单
 * @param {String} id
 * @param {[String]} params.refund_ids // 需要退款的所有退款 _id
 */
export function getBatchRefundList(params) {
  return request({
    url: '/order_refund/batch/check',
    method: 'get',
    params
    // mock: '/order_refund/batch/check'
  })
}

/**
 * 批量同意退款
 * @param {[String]} params.refund_ids // 需要退款的所有退款 _id
 */

export function batchAgreeRefund(data) {
  return request({
    url: '/order_refund/batch/agree',
    method: 'put',
    data
    // mock: '/order_refund/batch/agree'
  })
}
