<template>
  <div class="cur-refund-row__row-desc">
    <el-checkbox :value="rowData._checked" @change="handleCheck" /><span
      class="cur-refund-row__refund-id"
      >退款号：{{ rowData.refundId }}</span
    >
    <span class="cur-refund-row__order_id">订单号：{{ rowData.orderId }}</span>
  </div>
</template>

<script>
export default {
  props: {
    rowData: {
      type: Object,
      required: true
    },
    field: {
      type: String,
      required: true
    },
    index: {
      type: Number,
      required: true
    }
  },
  methods: {
    handleCheck(value) {
      const params = {
        type: 'order-check-change',
        index: this.index,
        rowData: this.rowData,
        _checked: value
      }
      this.$emit('on-custom-comp', params)
    }
  }
}
</script>

<style lang="scss">
.cur-refund-row__row-desc {
  .el-checkbox__input.is-checked + .el-checkbox__label {
    color: inherit;
  }
}
</style>
<style lang="scss" scoped>
.cur-refund-row {
  &__row-desc {
    padding-left: 20px;
    text-align: left;
    background: #f6f7fb;
  }
  &__refund-id {
    margin-right: 60px;
    margin-left: 6px;
  }
}
</style>
