<template>
  <div class="refund__refund-Status-col">
    <el-button
      class="refund__refund-Status-col__item refund__refund-Status-col__btn refund__refund-Status-col__refund-Status-btn"
      type="text"
      @click.stop.prevent="seeDetail"
    >
      {{ rowData.refundStatus | refundStatusFilter }}
    </el-button>
    <el-button
      v-if="showDiffRefundStatusArr.indexOf(rowData.refundStatus) !== -1"
      class="refund__refund-Status-col__item refund__refund-Status-col__btn refund__refund-Status-col__diff-btn"
      type="text"
      @click.stop.prevent="seeDetail"
    >
      {{ rowData.applyDate | expireDateDiffLocal }}
    </el-button>
    <el-button
      v-if="rowData.refundStatus === 'LACK_TRANSFER_CONDITION'"
      class="refund__refund-Status-col__item refund__refund-Status-col__btn refund__refund-Status-col__apply-transfer-btn cherry"
      type="text"
      @click.stop.prevent="applyTransfer"
      >申请转账</el-button
    >
    <success-manual
      v-else-if="rowData.refundStatus === 'FAIL_TRANSFER_ACCOUNT'"
      :id="rowData.id"
      v-model="successManualDialog"
      class="refund__refund-Status-col__item"
      @closedAfter="uploadHandlerResult"
    >
      <el-button
        class="refund__refund-Status-col__btn cherry"
        type="text"
        @click.stop.prevent="successManualDialog = true"
        >上传处理结果</el-button
      >
    </success-manual>
  </div>
</template>

<script>
import SuccessManual from '../components/SuccessManual'

import { DateTime } from 'luxon'

import { refundStatusFilter } from '../filters'

export default {
  components: { SuccessManual },
  filters: {
    refundStatusFilter,
    expireDateDiffLocal(value) {
      // value 是一个中国标准时间
      if (!value) return ''
      const expireDate = DateTime.fromMillis(
        DateTime.fromJSDate(new Date(value)).ts + 7 * 24 * 3600 * 1000
      )
      const start = DateTime.local()
      let diff = ''
      let diffInDays = null
      if (start.ts <= expireDate.ts) {
        diff += '剩'
        diffInDays = expireDate.diff(start, ['days', 'hours', 'minutes'])
      } else {
        diff += '延迟'
        diffInDays = start.diff(expireDate, ['days', 'hours', 'minutes'])
      }
      const diffObj = diffInDays.toObject()
      if (diffObj.days > 0) diff += diffObj.days + '天'
      if (diffObj.days > 0 || diffObj.hours > 0) diff += diffObj.hours + '小时'
      if (diffObj.days > 0 || diffObj.hours > 0 || diffObj.minutes > 0)
        diff += Math.floor(diffObj.minutes) + '分'
      return diff
    }
  },
  props: {
    rowData: {
      type: Object,
      required: true
    },
    field: {
      type: String,
      required: true
    },
    index: {
      type: Number,
      required: true
    }
  },
  data() {
    return {
      showDiffRefundStatusArr: [
        'APPLY_PENDING',
        'CONFIRMED',
        'PENDING_TRANSFER_ACCOUNT',
        'FAIL_TRANSFER_ACCOUNT'
      ],
      successManualDialog: false
    }
  },
  methods: {
    seeDetail() {
      // 参数根据业务场景随意构造
      const params = {
        type: 'detail',
        index: this.index,
        rowData: this.rowData
      }
      this.$emit('on-custom-comp', params)
    },
    applyTransfer() {
      const params = { type: 'apply-transfer', rowData: this.rowData }
      this.$emit('on-custom-comp', params)
    },
    uploadHandlerResult() {
      const params = { type: 'upload-handler-result', rowData: this.rowData }
      this.$emit('on-custom-comp', params)
    }
  }
}
</script>

<style lang="scss" scoped>
.refund__refund-Status-col {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  line-height: 1;
  color: #0085ff;
  flex-direction: column;
  .cherry {
    color: #f8747f;
  }
  .el-button {
    padding-top: 0;
    padding-bottom: 0;
  }
  .el-button + .el-button {
    margin-left: 0;
  }
  &__item + &__item {
    padding-top: 8px;
  }
  &__btn {
    line-height: 14px;
  }
}
</style>
