<template>
  <div class="search">
    <el-form
      label-position="left"
      label-width="76px"
      :model="formData"
      class="search__form"
    >
      <el-row :gutter="40">
        <el-col :span="5">
          <el-form-item label="用户ID">
            <el-input
              v-model="formData.userId"
              placeholder="请输入用户ID"
              size="small"
              clearable
            />
          </el-form-item>
        </el-col>
        <el-col :span="9">
          <el-form-item label="申请时间">
            <el-date-picker
              v-model="formData.applyDateRange"
              type="datetimerange"
              start-placeholder="请选择时间范围起始"
              end-placeholder="请选择时间范围结束"
              :default-time="['00:00:00', '23:59:59']"
              size="small"
              clearable
            />
          </el-form-item>
        </el-col>
        <el-col :span="5">
          <el-form-item label="订单号">
            <el-input
              v-model="formData.orderId"
              placeholder="请输入订单号"
              size="small"
              clearable
            />
          </el-form-item>
        </el-col>
      </el-row>
      <el-row :gutter="40">
        <el-col :span="5">
          <el-form-item label="退款号">
            <el-input
              v-model="formData.refundId"
              placeholder="请输入退款号"
              size="small"
              clearable
            />
          </el-form-item>
        </el-col>
        <el-col :span="8">
          <el-form-item label="退款金额">
            <range-ui>
              <el-input-number
                slot="start"
                controls-position="right"
                :min="0"
                v-model="formData.refundCountMin"
                type="number"
                placeholder="请输入退款金额（元）"
                size="small"
                clearable
              />
              <el-input-number
                slot="end"
                controls-position="right"
                :min="0"
                v-model="formData.refundCountMax"
                type="number"
                placeholder="请输入退款金额（元）"
                size="small"
                clearable
              />
            </range-ui>
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item label="订单名称">
            <OrderNameSelect
              v-model="formData.orderName"
              :getRemoteData="true"
              :placeholder="'请选择订单名称'"
            >
            </OrderNameSelect>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row :gutter="40">
        <el-col :span="5">
          <el-form-item label="订单类型">
            <el-select
              v-model="formData.orderType"
              placeholder="请选择订单类型"
              size="small"
              clearable
            >
              <el-option
                v-for="item in selectOptsMaps.orderTypes"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              />
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="5">
          <el-form-item label="退款状态">
            <el-select
              v-model="formData.refundStatus"
              placeholder="请选择退款状态"
              size="small"
              clearable
            >
              <el-option
                v-for="item in selectOptsMaps.allRefundStatus"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              />
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="5">
          <el-form-item label="退款原因">
            <el-select
              v-model="formData.refundReason"
              placeholder="请选择退款原因"
              size="small"
              clearable
            >
              <el-option
                v-for="item in allRefundReasons"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              />
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="5">
          <el-button type="primary" size="small" @click="handleSearch"
            >搜索</el-button
          >
          <el-button type="primary" plain size="small" @click="handleReset"
            >重置选项</el-button
          >
        </el-col>
      </el-row>
    </el-form>
  </div>
</template>
<script>
import RangeUi from '@/components/RangeUi'
import OrderNameSelect from '@/components/Select/OrderNamesSelect'
const defaultformData = {
  userId: '',
  applyDateRange: [],
  orderId: '',
  refundId: '',
  refundCountMin: '',
  refundCountMax: '',
  orderName: '',
  orderType: '',
  refundStatus: '',
  refundReason: ''
}

export default {
  components: { RangeUi, OrderNameSelect },
  props: {
    curRefundStatus: {
      type: String,
      default: '',
      validator(value) {
        return (
          value === '' ||
          [
            'APPLY_PENDING',
            'CLOSED',
            'REFUSED',
            'CONFIRMED',
            'LACK_TRANSFER_CONDITION',
            'PENDING_TRANSFER_ACCOUNT',
            'FAIL_TRANSFER_ACCOUNT',
            'SUCCESS_MANUAL',
            'SUCCESS_AUTO'
          ].indexOf(value) > -1
        )
      }
    },
    localFormData: {
      type: Object,
      default: () => {}
    },
    selectOptsMaps: {
      type: Object,
      required: true,
      default: () => ({
        orderNames: [],
        allRefundStatus: [],
        orderTypes: [],
        adminRefundReasons: [],
        weappRefundReasons: []
      })
    }
  },
  data() {
    return {
      formData: {}
    }
  },
  computed: {
    allRefundReasons() {
      return this.selectOptsMaps.weappRefundReasons
        .filter(opt => opt.value !== 'other')
        .concat(this.selectOptsMaps.adminRefundReasons)
    }
  },
  watch: {
    curRefundStatus(value) {
      this.formData.refundStatus = value
    }
  },
  created() {
    this.formData = Object.assign({}, defaultformData, this.localFormData)
  },
  methods: {
    handleSearch() {
      this.$emit('search', { searchParams: this.formData })
    },
    handleReset() {
      this.handleResetFormData()
      this.handleSearch()
    },
    handleResetFormData() {
      this.formData = Object.assign({}, defaultformData)
    }
  }
}
</script>

<style lang="scss">
.search {
  &__form {
    margin-bottom: -20px;
    .el-form-item {
      margin-bottom: 20px;
      &__label,
      &__content {
        height: 32px;
        line-height: 32px;
      }
    }
    .el-date-editor,
    .el-select {
      width: 100%;
    }
  }
}
</style>

<style lang="scss" scoped>
.search {
  &__form {
    &__source {
      display: flex;
      align-items: center;
    }
    &__source-scene,
    &__source-custom-value {
      width: 50%;
    }
    &__source-scene {
      padding-right: 14px;
    }
    &__source-custom-value {
      padding-left: 14px;
    }
  }
}
</style>
