<template>
  <div class="refund">
    <div class="refund__search">
      <el-button
        class="refund__search__collapse-btn"
        type="text"
        @click="searchFormShow = !searchFormShow"
      >
        {{ searchFormShowText }}
        <i
          class="el-icon--right"
          :class="searchFormShow ? 'el-icon-arrow-up' : 'el-icon-arrow-down'"
        />
      </el-button>
      <el-collapse-transition>
        <search-form
          v-show="searchFormShow"
          ref="search-form"
          :cur-refund-status="curRefundStatus"
          :local-form-data="LocalFormData"
          :select-opts-maps="selectOptsMaps"
          @search="handleSearch"
        />
      </el-collapse-transition>
    </div>
    <div class="refund__body">
      <el-tabs v-model="curTabName" @tab-click="handleTabClick">
        <el-tab-pane
          v-for="{ label, name, num } in tabArr"
          :key="name"
          :name="name"
        >
          <el-badge slot="label" :value="num" :max="99" class="item">
            <el-button
              :class="
                [
                  'APPLY_PENDING',
                  'LACK_TRANSFER_CONDITION',
                  'FAIL_TRANSFER_ACCOUNT'
                ].indexOf(name) !== -1
                  ? 'cherry'
                  : ''
              "
              type="text"
              size="small"
              >{{ label }}</el-button
            >
          </el-badge>
        </el-tab-pane>
      </el-tabs>
      <div class="refund__content">
        <div class="refund__content__quick-handler">
          <el-checkbox
            v-model="selectAll"
            class="refund__content__quick-handler__checkbox-hide"
            @change="handleSelectAll"
            >全选</el-checkbox
          >
          <el-button
            class="refund__content__quick-handler__batch-refund-btn"
            type="text"
            @click="handleBatchRefund"
            >批量同意退款</el-button
          >
        </div>
        <v-table
          is-horizontal-resize
          style="width:100%"
          class="refund__body__table"
          :is-loading="loading"
          :columns="columns"
          :table-data="orderList"
          :show-vertical-border="false"
          :cell-merge="cellMerge"
          :title-row-height="50"
          :merge-row-height="48"
          :row-height="70"
          @on-custom-comp="customCompFunc"
        />
        <div class="refund__body__pagination">
          <el-pagination
            :current-page="page.currentPage"
            :page-sizes="pageSizes"
            :page-size="page.pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="page.total"
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
          />
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import {
  getRefundList as getRefundListApi,
  getRefundProcess as getRefundProcessApi,
  getRefundListParams as getRefundListParamsApi,
  reTransfer as reTransferApi
} from '@/api/refund'

import Vue from 'vue'
import VTable from '@/easytable/v-table'

import curRefundRow from './cur-refund-row'
import applyDateTitle from './apply-date-title'
import refundReasonCol from './refund-reason-col'
import refundStatusCol from './refund-status-col'

import SearchForm from './search-form'

import { deepCopy, localSave, localRead } from '@/utils'
import { formatTime } from '@/utils/moment'
import { on, off } from '@/utils/tools'
import { statusUpdateMessage } from '@/utils/statusUpdateMessage'

import { fenToYuan, RMBSymbol } from '@/filters'
import { refundStatusFilter, refundReasonFilter } from '../filters'

Vue.component('cur-refund-row', curRefundRow)
Vue.component('refund-reason-col', refundReasonCol)
Vue.component('refund-status-col', refundStatusCol)

const refundStatusToTabLabel = {
  APPLY_PENDING: '退款申请待处理',
  LACK_TRANSFER_CONDITION: '待申请转账',
  FAIL_TRANSFER_ACCOUNT: '到账失败'
}

export default {
  components: { SearchForm, VTable },
  data() {
    const that = this
    return {
      selectOptsMaps: {
        orderNames: [],
        allRefundStatus: [],
        orderTypes: [],
        adminRefundReasons: [],
        weappRefundReasons: []
      },
      searchFormShow: true,
      curTabName: 'all',
      orderProcess: {},
      orderList: [],
      loading: false,
      page: {
        total: 0,
        pageSize: 10,
        currentPage: 1
      },
      pageSizes: [10, 20, 50],
      columns: [
        {
          field: 'orderName',
          title: '订单名称',
          width: 130,
          titleAlign: 'center',
          columnAlign: 'center',
          isResize: true
        },
        {
          field: 'orderType',
          title: '订单类型',
          width: 100,
          titleAlign: 'center',
          columnAlign: 'center',
          isResize: true
        },
        {
          field: 'payPrice',
          title: '交易金额',
          width: 100,
          formatter: function(rowData, rowIndex, pagingIndex, field) {
            return `<span>${RMBSymbol(fenToYuan(rowData.payPrice))}</span>`
          },
          titleAlign: 'center',
          columnAlign: 'center',
          isResize: true
        },
        {
          field: 'refundCount',
          title: '退款金额',
          width: 100,
          formatter: function(rowData, rowIndex, pagingIndex, field) {
            return `<span>${RMBSymbol(fenToYuan(rowData.refundCount))}</span>`
          },
          titleAlign: 'center',
          columnAlign: 'center',
          isResize: true
        },
        {
          field: 'applyDate',
          title: '申请时间',
          titleComponent: {
            render: h =>
              h(applyDateTitle, {
                props: {
                  activeCommand: that.applyTimeSortType
                },
                on: {
                  sortTypeChange: command => that.handleADTSort(command)
                }
              })
          },
          width: 170,
          titleAlign: 'center',
          columnAlign: 'center',
          formatter: function(rowData, rowIndex, pagingIndex, field) {
            return formatTime(rowData.applyDate)
          },
          isResize: true
        },
        {
          field: 'cur-refund-reason',
          title: '原因',
          width: 240,
          titleAlign: 'center',
          columnAlign: 'center',
          isResize: true,
          componentName: 'refund-reason-col'
        },
        {
          field: 'refundStatus',
          title: '退款状态',
          width: 140,
          titleAlign: 'center',
          columnAlign: 'center',
          componentName: 'refund-status-col',
          isResize: true
        },
        {
          field: 'userBaseInfo',
          title: '用户',
          width: 100,
          formatter: function(rowData, rowIndex, pagingIndex, field) {
            return `
              <div class="flex-col-center">
                <span>${rowData.userId}</span>
                <span>${rowData.userNickName}</span>
              </div>
            `
          },
          titleAlign: 'center',
          columnAlign: 'center',
          isResize: true
        }
      ],
      LocalFormData: {},
      curFilterQuery: {
        searchParams: {}
      },
      selectAll: false,
      applyTimeSortType: ''
    }
  },
  computed: {
    searchFormShowText() {
      return this.searchFormShow ? '收起' : '展开搜索条件'
    },
    tabArr() {
      const successTabLabel = '退款成功'
      return [
        {
          label: '全部',
          name: 'all'
        }
      ].concat(
        (this.selectOptsMaps.allRefundStatus || []).reduce(
          (acc, { label, value }) => {
            if (refundStatusToTabLabel[value]) {
              acc.push({
                label: refundStatusToTabLabel[value],
                name: value,
                num: this.orderProcess[value] || ''
              })
            }
            if (value === 'SUCCESS_MANUAL' || value === 'SUCCESS_AUTO') {
              const successTabIndex = acc.filter(
                ({ name }) => name === 'SUCCESS'
              )[0]
              if (!successTabIndex) {
                acc.push({
                  label: successTabLabel,
                  name: 'SUCCESS'
                })
              }
            }
            return acc
          },
          []
        )
      )
    },
    curRefundStatus() {
      return this.curTabName === 'all' || this.curTabName === 'SUCCESS'
        ? ''
        : this.curTabName
    }
  },
  watch: {
    applyTimeSortType() {
      this.handleSearch()
    }
  },
  created() {
    this.comInit()
    this.getOrderListParams()
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      if (!from.path.includes('/trade/refund')) {
        vm.$refs['search-form'].handleReset()
      } else {
        vm.handleSearch(vm.curFilterQuery)
      }
    })
  },
  mounted() {
    on(window, 'beforeunload', this.beforeunloadFn)
  },
  destroyed() {
    off(window, 'beforeunload', this.beforeunloadFn)
  },
  methods: {
    beforeunloadFn(e) {
      localSave('refund-filter-query', '')
    },
    comInit() {
      // this.pageSizes = Array.apply(null, { length: 20 }).reduce((acc, item, index) => acc.concat(5 * (index + 1)), [])
      this.getLocalQuery()
    },
    handleSelectAll(value) {
      this.orderList = this.orderList.map(order => {
        if (order._copy) {
          order._checked = value
        }
        return order
      })
    },
    cellMerge(rowIndex, rowData, field) {
      if (rowIndex % 2 === 0 && field === 'orderName') {
        return {
          colSpan: 9,
          rowSpan: 1,
          componentName: 'cur-refund-row'
        }
      }
    },
    getLocalQuery() {
      const localQuery = localRead('refund-filter-query')
        ? JSON.parse(localRead('refund-filter-query'))
        : {}
      if (localQuery.disabled) {
        return
      } else {
        delete localQuery.disabled
      }
      if (deepCopy(localQuery)) this.curFilterQuery = deepCopy(localQuery)
      if (localQuery.currentPage) this.page.currentPage = localQuery.currentPage
      if (localQuery.pageSize) this.page.pageSize = localQuery.pageSize
      if (localQuery.searchParams) {
        localQuery.searchParams.refundStatus =
          localQuery.searchParams.refundStatus === 'all'
            ? ''
            : localQuery.searchParams.refundStatus
        this.LocalFormData = localQuery.searchParams || {}
      }
    },
    async getOrderListParams() {
      const { data } = await getRefundListParamsApi()
      Object.keys(data).forEach(_key => {
        if (_key !== 'order_names') {
          data[_key] = data[_key].map(({ key, value }) => {
            let label = ''
            switch (key) {
              case 'refund_status':
                label = refundStatusFilter(key)
                break
              case 'refund_reasons':
                label = refundReasonFilter(key)
                break
              default:
                label = value
            }
            return {
              label,
              value: key
            }
          })
        }
      })
      this.selectOptsMaps = {
        orderNames: data.order_names,
        orderTypes: data.order_types,
        allRefundStatus: data.refund_status,
        adminRefundReasons: data.refund_reasons,
        weappRefundReasons: data.weapp_refund_reasons
      }
    },
    async getRefundProcess() {
      const { data } = await getRefundProcessApi()
      this.orderProcess = deepCopy(data)
    },
    getSearchQuery(filterParams) {
      const query = {}
      const orderFilterQuery = deepCopy(filterParams)
      orderFilterQuery.disabled = true
      localSave('refund-filter-query', JSON.stringify(orderFilterQuery))
      if (filterParams.currentPage)
        query.current_page = filterParams.currentPage
      if (filterParams.pageSize) query.page_size = filterParams.pageSize
      if (this.applyTimeSortType === 'default') {
        query.sort_by = 1
      } else if (this.applyTimeSortType === 'overtime') {
        query.sort_by = 2
      }
      const searchFilterParams = filterParams.searchParams
      if (searchFilterParams) {
        if (searchFilterParams.userId) query.user_id = searchFilterParams.userId
        if (
          searchFilterParams.applyDateRange &&
          searchFilterParams.applyDateRange.length >= 2
        ) {
          query.apply_at_begin = searchFilterParams.applyDateRange[0]
          query.apply_at_end = searchFilterParams.applyDateRange[1]
        }
        if (searchFilterParams.orderId)
          query.order_id = searchFilterParams.orderId
        if (searchFilterParams.refundCountMin)
          query.refund_amount_min = searchFilterParams.refundCountMin
        if (searchFilterParams.refundCountMax)
          query.refund_amount_max = searchFilterParams.refundCountMax
        if (searchFilterParams.refundId)
          query.refund_id = searchFilterParams.refundId
        if (searchFilterParams.orderType)
          query.order_type = searchFilterParams.orderType
        if (searchFilterParams.orderName)
          query.order_name = searchFilterParams.orderName
        if (
          searchFilterParams.refundStatus &&
          searchFilterParams.refundStatus !== 'all'
        )
          query.refund_status = searchFilterParams.refundStatus
        if (searchFilterParams.refundReason)
          query.refund_reason = searchFilterParams.refundReason
      }
      return query
    },
    async handleSearch(query) {
      this.loading = true
      try {
        const _query = Object.assign({}, query)
        if (!_query.currentPage) _query.currentPage = this.page.currentPage
        if (!_query.pageSize) _query.pageSize = this.page.pageSize
        if (!_query.searchParams)
          _query.searchParams = Object.assign(
            this.curFilterQuery.searchParams || {},
            { refundStatus: this.curTabName }
          )
        this.curFilterQuery = deepCopy(_query)
        const { data } = await getRefundListApi(this.getSearchQuery(_query))
        if (data.list.length === 0 && this.page.currentPage > 1) {
          this.handleSearch(
            Object.assign(_query, {
              currentPage: 1
            })
          )
          this.setPage({ currentPage: 1, pageSize: 10, total: 0 })
          return
        } else {
          this.setPage({ ...data })
        }
        this.setTableData({ ...data })
        await this.handleSearchAfter(_query.searchParams.refundStatus)
        this.loading = false
      } catch (err) {
        console.error(err)
        this.loading = false
      }
    },
    async handleSearchAfter(refundStatus) {
      this.curTabName = refundStatus || 'all'
      this.selectAll = false
      await this.getRefundProcess()
    },
    setPage({ current_page: currentPage, page_size: pageSize, count: total }) {
      this.page.currentPage = currentPage
      this.page.pageSize = pageSize
      this.page.total = total
    },
    async setTableData({ list }) {
      // const localBatchRefundIds = localRead('batch-refund-ids') || []
      this.orderList = list
        .map(order => ({
          orderName: order.order_name,
          orderType: order.order_type,
          payPrice: order.pay_price,
          refundCount: order.refund_amount,
          applyDate: order.apply_at,
          refundReason: order.refund_reason,
          refundReasonValue: order.refund_reason_value,
          refundOtherReasonDescribe: order.other_reason_message,
          refundStatus: order.status,
          orderId: order.order_id,
          refundId: order.refund_id,
          userId: order.user.user_id,
          userNickName: order.user.nickname,
          id: order._id
        }))
        .reduce(
          (acc, order) =>
            acc.concat([
              Object.assign(deepCopy(order), {
                // _checked: localBatchRefundIds.indexOf(order.id) !== -1
                _checked: false,
                _copy: true
              }),
              deepCopy(order)
            ]),
          []
        )
    },
    async handleCurrentChange(page) {
      await this.handleSearch({
        currentPage: page
      })
    },
    async handleSizeChange(pageSize) {
      await this.handleSearch({
        pageSize
      })
    },
    async handleTabClick(name) {
      this.applyTimeSortType = ''
      this.$refs['search-form'].handleResetFormData()
      this.curFilterQuery = {}
      await this.handleSearch({
        currentPage: 1,
        pageSize: 10
      })
    },
    async customCompFunc(params) {
      if (params.type === 'detail') {
        this.$router.push({
          name: 'RefundDetail',
          params: { id: params.rowData.id }
        })
      } else if (params.type === 'order-check-change') {
        const curOrderInfo = deepCopy(this.orderList[params.index])
        curOrderInfo._checked = params._checked
        this.$set(this.orderList, params.index, curOrderInfo)
      } else if (params.type === 'apply-transfer') {
        await this.handleReTransfer(params.rowData.id)
        this.handleSearch()
      } else if (params.type === 'upload-handler-result') {
        this.handleSearch()
      }
    },
    handleBatchRefund() {
      const checkedOrderArr = this.orderList.filter(order => order._checked)
      if (checkedOrderArr.length === 0) {
        this.$message({
          type: 'info',
          message: '请选择批量同意退款的订单!',
          duration: 5000
        })
        return
      } else if (
        checkedOrderArr.some(order => order.refundStatus !== 'APPLY_PENDING')
      ) {
        this.$message({
          type: 'info',
          dangerouslyUseHTMLString: true,
          message:
            '<span>包含不能批量退款的订单，请重新选择<br>请筛选出状态为退款申请待处理的订单进行批量同意退款操作</span>',
          duration: 5000
        })
      } else {
        localSave(
          'batch-refund-ids',
          checkedOrderArr.map(order => order.id).join(',')
        )
        this.$router.push({ name: 'RefundBatchHandleRefund' })
      }
    },
    handleADTSort(sortType) {
      this.applyTimeSortType = sortType
    },
    handleReTransfer(id) {
      reTransferApi(id).then(async res => {
        statusUpdateMessage(res)
      })
    }
  }
}
</script>

<style lang="scss">
@import '~@/styles/mixin.scss';
@import '~@/styles/variables.scss';

.refund {
  .el-badge__content {
    background: #f8747f;
    &.is-fixed {
      transform: translateY(-30%) translateX(100%) scale(0.7);
    }
  }
  .flex-col-center {
    height: 100%;
    line-height: 22px;
  }
  .el-tabs {
    &__header {
      margin-bottom: 0;
    }
    &__nav-wrap {
      padding-left: 30px;
      &::after {
        height: 1px;
      }
    }
    &__item {
      padding: 5px 16px;
      height: 50px;
      &.is-active {
        .el-button--text {
          color: #{$activeColor};
        }
      }
      .el-button--text {
        font-size: 14px;
        color: #{$textLightBlock};
        &.cherry {
          color: #f8747f;
        }
      }
      .el-badge {
        padding: 0 16px;
        &__content {
          background: #f8747f;
          &.is-fixed {
            transform: translateY(10%) translateX(50%) scale(0.7);
          }
        }
      }
    }
  }
  .v-table-views,
  .v-table-header {
    border: none;
  }
  .v-table-header {
    @include ff-medium;

    font-size: 14px;
    color: #17233d;
  }
  .v-table-empty-content,
  .v-table-body {
    @include ff-regular;

    font-size: 14px;
    color: #576277;
  }
  .v-table-body-cell {
    padding: 0;
  }
  .v-table-row {
    padding: 10px 0;
  }
}
</style>

<style lang="scss" scoped>
@import '~@/styles/variables.scss';

.refund {
  .cherry {
    color: #f8747f;
  }
  &__search {
    position: relative;
    padding-bottom: 30px;
    &__collapse-btn {
      position: absolute;
      top: -12px;
      right: 0;
      z-index: 99;
    }
  }
  &__body {
    border: 1px solid #ebeef5;
    border-radius: 4px;
    &__pagination {
      padding: 50px 0 50px;
      text-align: center;
    }
  }
  &__content {
    padding: 0 20px;
    &__quick-handler {
      padding: 30px 20px 20px;
      &__batch-refund-btn {
        padding-top: 0;
        padding-bottom: 0;
      }
      &__checkbox-hide {
        color: #409eff;
      }
    }
  }
}
</style>
