<template>
  <el-dropdown
    class="apply-date-title"
    trigger="click"
    @command="handleCommand"
  >
    <span class="apply-date-title">
      申请时间<i class="el-icon-arrow-down el-icon--right" />
    </span>
    <el-dropdown-menu
      slot="dropdown"
      class="apply-date-title__el-dropdown-menu"
    >
      <el-dropdown-item
        v-for="item in dropdownOpts"
        :key="item.command"
        class="apply-date-title__el-dropdown-menu__item"
        :class="{ active: activeCommand === item.command }"
        :command="item.command"
        >{{ item.text }}</el-dropdown-item
      >
    </el-dropdown-menu>
  </el-dropdown>
</template>

<script>
export default {
  props: {
    activeCommand: {
      type: String,
      default: 'default',
      validator(value) {
        return ['', 'default', 'overtime'].indexOf(value) !== -1
      }
    }
  },
  data() {
    return {
      dropdownOpts: [
        {
          command: 'default',
          text: '最近申请时间'
        },
        {
          command: 'overtime',
          text: '临近超时'
        }
      ]
    }
  },
  methods: {
    handleCommand(command) {
      this.$emit('sortTypeChange', command)
    }
  }
}
</script>

<style lang="scss" scoped>
@import '~@/styles/variables.scss';
@import '~@/styles/mixin.scss';

.apply-date-title {
  font-size: 14px;
  @include ff-medium;

  color: #{$textDeepBlock};
  &__el-dropdown-menu {
    &__item.active {
      color: #{$activeColor};
    }
  }
}
</style>
