export function refundStatusFilter(value) {
  switch (value) {
    case 'APPLY_PENDING':
      return '退款申请待处理'
    case 'CLOSED':
      return '退款已关闭'
    case 'REFUSED':
      return '退款已被拒绝'
    case 'CONFIRMED':
      return '退款申请已确认'
    case 'LACK_TRANSFER_CONDITION':
      return '退款转账条件不足'
    case 'PENDING_TRANSFER_ACCOUNT':
      return '退款待到账'
    case 'FAIL_TRANSFER_ACCOUNT':
      return '退款到账失败'
    case 'SUCCESS_MANUAL':
      return '退款成功(人工)'
    case 'SUCCESS_AUTO':
      return '退款成功(自动)'
    default:
      throw new Error(`无法识别的退款状态: ${value}`)
  }
}

export function refundReasonFilter(value) {
  switch (value) {
    case 'misdiagnose':
      return '误诊'
    case 'user_asked':
      return '用户要求退款'
    case 'other':
      return '其它'
    default:
      throw new Error(`无法识别的退款原因: ${value}`)
  }
}
